import React, { Component } from 'react';
import $ 					from 'jquery';

class SeriesSeasonsListing extends Component {

    // constructor() {
    //     super();
    // };

    toggleClick(target) {

    	$('#' + target).slideToggle();

    };

  	render() {

        var styles = {
            initial: {
                display: "none"
            }
        };

    	return (
	    	<li className="list-group-item"
                onClick={this.toggleClick.bind(this, "detail-" + this.props.id)}>
                <div    className="row toggle" 
                        id="dropdown-detail-1" 
                        data-toggle={"detail-" + this.props.id} >

                        <div className="col-xs-10">
                            { this.props.id }
                        </div>
                        <div className="col-xs-2"><i className="fa fa-chevron-down pull-right"></i></div>
                </div>
                <div id={"detail-" + this.props.id} style={styles.initial}>
                    <hr></hr>
                    <div className="container">
                        <div className="fluid-row">
                            <div className="col-xs-6">
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                            </div>
                            <div className="col-xs-6">
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                            </div>
                            <div className="col-xs-6">
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                            </div>
                            <div className="col-xs-6">
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                            </div>
                        </div>
                    </div>
                </div>
            </li>
	    );
  }
}

export default SeriesSeasonsListing;


