import React, { Component } 								from 'react';
import GetImage             								from '../partials/GetImage';
import { Link } 											from 'react-router-dom';
// import { IndexRoute, browserHistory } 						from 'react-router';

class ListingGenerator extends Component {

    // constructor(props) {
    //     super(props);
    // };

	createUrl(seriesName) {
		return seriesName.toLowerCase().replace(/[^a-zA-Z0-9]/g, '-');
	};

  	render() {

    	return (
	    	<div className="col-md-2">
		        <div className="productbox animated fadeInUp">
		        	<div className="bs-image-container text-center">
		            	<GetImage className="img-responsive img-thumbnail animated fadeIn" img={ this.props.imgUrl } name={ this.props.name } />
	            	</div>
		            <div className="producttitle" title={ this.props.name }>
		            	<p className="text-primary text-center"> { this.props.name } </p>
	            	</div>
		            <address className="productaddress">
		                <strong>Host: { this.props.host }</strong>
		                <strong className="pull-right">Price: { '$' + this.props.price }</strong>
		            </address>
		            <div className="productprice">
		                <div className="pull-right">
		                    <Link 
		                    		to={"series-listing/" + this.createUrl(this.props.name)}
		                    		className="btn btn-success btm-sm" role="button">

		                    		View <i className="fa fa-television"></i>
		                    </Link>
		                </div>
		                <div className="pricetext">
		                    <a className="btn btn-warning btm-sm" role="button"><i className="fa fa-info-circle"></i> Info</a>
		                </div>
		            </div>
		        </div>
	        </div>
	    );
  }
}

export default ListingGenerator;


